import VideoButton from './components/videoButton';
import { isSelectedBlock } from '@ali/4ever-utils';

export default function toolbar(config) {
  const videoConfig = config.video || {};
  const videoLocaleConfig = videoConfig.locale || {};
  return {
    buttons: {
      default: VideoButton,
    },
    locale: videoLocaleConfig,
    getTarget: (controller) => {
      if (isSelectedBlock(controller.value, 'embed')) {
        return 'embed';
      }
      return undefined;
    },
  };
}
